import React,{Component} from 'react';
import '../../iconfont.css'
import './navstyle.css';
export default class TopNav extends Component{
	render(){
		return <div className="top-nav"> 
		<div className="container">
			<TopNavLeft />
			<TopNavRight />
		</div>
		</div>
	}
}

class TopNavLeft extends Component{
		render(){
			return <ul className="top-nav-left">
		<li><a href="https://www.mi.com/index.html" target='_blank'></a></li>
		<li><a href="http://www.miui.com/" target='_blank'>MIUI</a></li>
		<li><a href="http://www.miliao.com/" target='_blank'></a></li>
		<li><a href="http://game.xiaomi.com/hy/index.html" target='_blank'></a></li>
		<li><a href="http://www.duokan.com/" target='_blank'></a></li>
		<li><a href="https://i.mi.com/" target='_blank'></a></li>
		<li><a href="https://jr.mi.com/index.html" target='_blank'></a></li>
		<li><a href="https://www.mi.com/appdownload/" target='_blank'></a></li>
		<li><a href="https://static.mi.com/feedback/" target='_blank'></a></li>
		</ul>
		}
}

class TopNavRight extends Component{
	render(){
		return <div className="top-nav-right">
				 <ul>
				 	<li><a href="https://account.xiaomi.com/" target='_self'></a></li>
				 	<li><a href="https://account.xiaomi.com/pass/register" target='_self'></a></li>
				 	<li><a href="https://account.xiaomi.com/" target='_self'></a></li>
				 </ul>
				 <TopNavBuyCar />
			   </div>
	}
}

class TopNavBuyCar extends Component{
	
	constructor(props){
		super(props);
		this.state={goodCount:0};
	}
	render(){
		return <div className="top-nav-buycar">
					<i className="iconfont">&#xe602;</i>
					<span>({this.state.goodCount})</span>
					<div className="buycar-tips"></div>
		       </div>
	}

}


